$(document).ready(function () {
    var form = document.querySelector('form.send');
    form.addEventListener('submit', function (e) {
        var input = document.querySelectorAll('.in input[data-inp="te"]');
        for (var i = 0; i < input.length; i++) {
            if (!input[i].value || !input[9].checked || !input[10].checked || !input[11].checked) {
                e.preventDefault();
                if (!input[i].value) {
                    input[i].nextSibling.style.display = 'block';
                    if (!input[9].checked && !input[10].checked && !input[11].checked) {
                        $(this).parent().parent().find('.alert').css({
                            'display': 'block'
                        })
                    }
                }
            } else {
                if (input[i].nextSibling.classList.contains('alert')) {
                    input[i].nextSibling.style.display = 'none';
                } else if (input[9].checked || input[10].checked || input[11].checked) {
                    $(this).parent().parent().find('.alert').css({
                        'display': 'none'
                    })
                }
            }
        }
    })
});